import React from 'react'
import { Box } from 'theme-ui'
import memphis from '../../assets/nbk2.png'

const styles = {
  pattern: {
    // size: `full`,
    // backgroundImage: `url(${memphis})`,
    // backgroundSize: `11rem`,
    // position: `absolute`,
    // opacity: 0.2,
    // top: 0,
    // left: 0
    maxWidth: '100%',
    height: 'auto',
    size: 'full',
    backgroundImage: `url(${memphis})`,
    backgroundSize: `cover`,
    position: `absolute`,
    opacity: 1,
    top: 0,
    left: 0
    // backgroundRepeat: `no-repeat`,
  }
}

const KsarPattern = ({ sx = {}, ...props }) => (
  <Box sx={{ ...styles.pattern, ...sx }} {...props}></Box>
)

export default KsarPattern
