import { graphql } from 'gatsby'
import PostsPage from '../containers/Photo'

export default PostsPage

export const pageQuery = graphql`
  query PostsPageQueryPic(
    $paginatePostsPage: Boolean!
    $skip: Int
    $limit: Int
    $includeExcerpt: Boolean!
    $includeTimeToRead: Boolean!
    $imageQuality: Int!
  ) {
    gettyPics: allArticle(
      filter: {
        private: { ne: true }
        draft: { ne: true }
        category: { id: { in: ["4ee7385c-096b-5021-babe-147911033d4b"] } }
      }
      sort: { fields: [title], order: DESC }
      limit: 100
    ) {
      nodes {
        ...ArticlePreview
        ...ArticleThumbnailFeatured
      }
    }

    bilignarPics: allArticle(
      filter: {
        private: { ne: true }
        draft: { ne: true }
        category: { id: { in: ["698398b7-a039-5595-b66d-6483d087430d"] } }
      }
      sort: { fields: [title], order: DESC }
      limit: 100
    ) {
      nodes {
        ...ArticlePreview
        ...ArticleThumbnailFeatured
      }
    }

    toutahPics: allArticle(
      filter: {
        private: { ne: true }
        draft: { ne: true }
        category: { id: { in: ["717d530d-f685-5aa7-bcbe-b989377d79b8"] } }
      }
      sort: { fields: [title], order: DESC }
      limit: 100
    ) {
      nodes {
        ...ArticlePreview
        ...ArticleThumbnailFeatured
      }
    }
    autresPics: allArticle(
      filter: {
        private: { ne: true }
        draft: { ne: true }
        category: { id: { in: ["6db2f0e8-0353-5e09-a2f6-11961333d44a"] } }
      }
      sort: { fields: [title], order: DESC }
      limit: 100
    ) {
      nodes {
        ...ArticlePreview
        ...ArticleThumbnailFeatured
      }
    }

    featuredPosts: allArticle(
      filter: {
        private: { ne: true }
        draft: { ne: true }
        featured: { eq: true }
        category: {
          id: {
            in: [
              "7c0c6ff9-07a8-5bde-a579-7d42fc0874fa"
              "67152c1a-551d-52b2-a8f1-c6c5f36c2fc4"
              "4f4c6676-555d-5334-b7ef-884b834eac21"
            ]
          }
        }
      }
      sort: { fields: [title], order: DESC }
      limit: 10
    ) {
      nodes {
        ...ArticlePreview
        ...ArticleThumbnailFeatured
      }
    }

    recentPosts: allArticle(
      filter: {
        private: { ne: true }
        draft: { ne: true }
        category: {
          id: {
            in: [
              "7c0c6ff9-07a8-5bde-a579-7d42fc0874fa"
              "67152c1a-551d-52b2-a8f1-c6c5f36c2fc4"
              "4f4c6676-555d-5334-b7ef-884b834eac21"
            ]
          }
        }
      }
      sort: { fields: [date], order: DESC }
      limit: 6
    ) {
      nodes {
        ...ArticlePreview
        ...ArticleThumbnailRegular
      }
    }

    paginatedPosts: allArticle(
      filter: {
        private: { ne: true }
        draft: { ne: true }
        category: {
          id: {
            in: [
              "7c0c6ff9-07a8-5bde-a579-7d42fc0874fa"
              "67152c1a-551d-52b2-a8f1-c6c5f36c2fc4"
              "4f4c6676-555d-5334-b7ef-884b834eac21"
            ]
          }
        }
      }
      sort: { fields: [date], order: DESC }
      limit: $limit
      skip: $skip
    ) @include(if: $paginatePostsPage) {
      nodes {
        ...ArticlePreview
        ...ArticleThumbnailRegular
      }
      ...ArticlePagination
    }
  }
`
