import React from 'react'
import { Box } from 'theme-ui'
import memphis from '../../assets/bilignard.png'

const styles = {
  pattern: {
    maxWidth: '100%',
    height: 'auto',
    size: 'full',
    backgroundImage: `url(${memphis})`,
    backgroundSize: `cover`,
    position: `absolute`,
    opacity: 0.3,
    top: 0,
    left: 0
  }
}

const MapPattern = ({ sx = {}, ...props }) => (
  <Box sx={{ ...styles.pattern, ...sx }} {...props}></Box>
)

export default MapPattern
